.card {
  box-sizing: border-box;
  width: 350px;
  border-radius: 5px;
  margin-top: 20px;
  // background: $bg-color;

  &__img{
    width: 100%;
    height: 200px;
    object-fit: cover;
  }

  &__title{
    font-size: 1.3em;
  }

  &__title,&__text{
    text-align: left;
    padding-left: 10px;
    letter-spacing: 1;
    opacity: 1;
  }
  &__btn {
    display: inline-block;
    text-decoration: none;
    padding-left: 10px;
  }
}

/* MediaQuery */
@media only screen and (max-width: $breakpoint-md) {
  .cover-card{
    margin-top: 0;
  }
  .card{
    flex-wrap: wrap;
    align-content: space-around;
  }
}
